.BackgroundVideo_Container__1yBNe {
    position: relative;
    min-height: 90vh;
    max-height: 100vh;
    overflow: hidden;
    z-index: 1000;
}

/* Style the video: 100% width and height to cover the entire window */
.BackgroundVideo_Video__yZ64S {
    width: 100%;
    height: 100%;
    opacity: 0.8;
  }
  
  .BackgroundVideo_title__ZzYlp{
    color: var(--nav) !important;
    font-size: 15rem !important;
    font-family: 'Playfair Display', serif !important;
  }
  /* Add some content at the bottom of the video/page */
  .BackgroundVideo_Content__2iYVS {
    position: absolute;
    top: 0;
    color:#f1f1f1;
    background-color: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  .BackgroundVideo_SubContent__2bIYo {
      text-align: center;
      padding: 10px;
    }
  .BackgroundVideo_SubContent__2bIYo p {
      color: #eeeeeead;
      font-size: 1.3rem;
  }
  .BackgroundVideo_SubContent__2bIYo button {
      font-weight: bold;
      margin-top: 30px;
      color: #f1f1f1;
      border: #f1f1f1 1px solid;
      padding: 10px 5px;
      background-color: hsla(0, 0%, 0%, 0);
      font-size: 1.25rem;
      border-radius: 5px;
  }
  .BackgroundVideo_SubContent__2bIYo button:hover {
      background-color: var(--nav);
      color: #f1f1f1;
      border: var(--nav) 1px solid;

  }

